@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: 'Noto Sans JP', sans-serif;
}

body {
  margin: 0;
  background-color: #3f3f3f;
}


.card {
    border-radius: 12px;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }


.forcast-list {
    list-style-type: none;
    margin-right: auto;
    margin-left: auto;
    width: 100%;
    display: flex;
    padding: 0;
}

.forcast-item {
    padding: 1rem;
    background-color: rgb(109, 106, 106);
    margin: 2rem auto;
    width:19%;
    /* max-width: 95%; */
  }

  

  @media screen and (max-width: 770px) {
    .forcast-list {
        flex-direction: column;

    }

    .forcast-item {
        width: 100%;

    }

    .forcast-item li{
        display: flex;
        justify-content: space-between;
    }
  }


.city {
    padding: 1rem;
    background-color: rgb(31, 31, 31);
    margin: 2rem auto;
    max-width: 95%;
  }

  h2,h3 {
    color: antiquewhite;
  }

  form {
    margin-top: 20px;
  }


  .input-container {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin-left: auto;
    margin-right: auto;
  }

  button.fav-btn {
    background: none;
    color: white;
    border: none;
    padding: 0;
    font: inherit;
    cursor: pointer;
    outline: inherit;
  }
.favorite-item   {
    display: flex;
    background-color: #918d8d;
    padding: 3px;
    margin-bottom: 5px;
    padding: 11px;
    align-items: center;
    border-radius: 6px;
    justify-content: space-between;
}


.favorite-item p {
    margin-bottom: 0;
    font-weight: bold;
    color: #fffbf5;
}

.favorite-item h3 {
    color: #fffbf5;

}



.favorite-item a {
    /* display: flex;
    align-items: center; */
    align-items: center;
    width: 90%;
    text-decoration: none;
    align-items: center;
    display: flex;
    justify-content: space-between;
}
.favorite-item a:hover{
    cursor: pointer;
}
.MainHeader_header__3jG-X {
    width: 100%;
    height: 5rem;
    background-color: #044599;
    padding: 0 10%;
  }
  
  .MainHeader_header__3jG-X nav {
    height: 100%;
  }
  
  .MainHeader_header__3jG-X ul {
    height: 100%;
    list-style: none;
    display: flex;
    padding: 0;
    margin: 0;
    align-items: center;
    justify-content: center;
  }
  
  .MainHeader_header__3jG-X li {
    margin: 0 1rem;
    width: 5rem;
  }
  
  .MainHeader_header__3jG-X a {
    color: white;
    text-decoration: none;
  }
  
  .MainHeader_header__3jG-X a:hover,
  .MainHeader_header__3jG-X a:active,
  .MainHeader_header__3jG-X a.MainHeader_active__11wpQ {
    color: #95bcf0;
    padding-bottom: 0.25rem;
    border-bottom: 4px solid #95bcf0;
  }
